//import env from 'dotenv'; //NOT REQUIRED SINCE PARCEL ALREADY LOADS THE .env FILES

class Security {
    constructor() {
        this._host = '';
        this._rootHost = '';

        //env.config(); //NOT REQUIRED SINCE PARCEL ALREADY LOADS THE .env FILES
        this.config();
        window.security = this;     
    }

    get host() {
        return this._host;
    }

    get rootHost() {
        return this._rootHost;
    }

    config() {
        this._host = process.env.host;
        this._rootHost = process.env.rootHost;
    }
}

export default Security;