export function getFormattedNow() {
    return formatDate(new Date());
};

export function formatDate(date) {
    return `${date.getFullYear()}-${format2Digits(date.getMonth() + 1)}-${format2Digits(date.getDate())}`;
}

function format2Digits(num) {
    if(num < 10) return `0${num}`;
    return num;
}

export function formatTime(date) {
    const finalString = `${format2Digits(date.getHours)}:${format2Digits(date.getMinutes)}`
    return finalString;
}

export function formatTimestamp(timestamp) {
    const date = new Date(timestamp);
    const finalString = `${format2Digits(date.getDate())}/${format2Digits(date.getMonth() + 1)}/${date.getFullYear()} ${formatAMPM(date)}`;
    return finalString;
}

export function formatAMPM(date) {
    let hours = date.getHours();
    let minutes = date.getMinutes();
    let seconds = date.getSeconds();

    const extension = hours >= 12 ? 'PM' : 'AM';

    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'

    hours = format2Digits(hours);
    minutes = format2Digits(minutes);
    seconds = format2Digits(seconds);

    const formattedTime = `${hours}:${minutes}:${seconds}${extension}`;
    return formattedTime;
}

Date.prototype.toDateInputValue = (function () {
    const local = new Date(this);
    local.setMinutes(this.getMinutes() - this.getTimezoneOffset());
    return local.toJSON().slice(0, 10);
});