import Axios from 'axios';

const key = () => {
    if(process.env.NODE_ENV === 'development') {
        // TODO: Fix the fetching of the AUTH_KEY on Windows
        // return process.env.AUTH_KEY;
        return '$2y$10$/69FXxMVChWiWlpY0FuoKeJmtQmxDcDhl4uBcoje1tt2IdVVaFU1W';
    }
    
    return sessionStorage.getItem('localkey');
}

export function getRequest(url) {
    return new Promise((resolve, reject) => {

        Axios.get(url, {
            headers: { authorization: key() }
        })
            .then(data => {
                resolve(data)
            })
            .catch(err => reject(err));

    });
}

export function postRequest(url, postdata) {
    return new Promise((resolve, reject) => {

        Axios.post(url, postdata, {
            headers: { authorization: key() }
        })
            .then(data => resolve(data))
            .catch(err => reject(err));

    });
}